import { Alert, Form, Input, Button, Checkbox, Layout, Row, Col, Tooltip, Spin } from 'antd';
import React from 'react';
import { Redirect } from "react-router-dom";
import { InfoCircleOutlined, UserOutlined, LoadingOutlined, HomeOutlined, MailOutlined, PhoneOutlined } from '@ant-design/icons';
import { Link } from "react-router-dom";
import logo from "../../../../src/assets/images/sp_logo.png"
import { useStoreActions, useStoreState } from '../../store/hooks/easyPeasy';
// const FormItem = Form.Item;
const antIcon = <LoadingOutlined style={{ fontSize: 24 }} spin />;

// interface IProps {
//     logInConnect: (cred: any) => void;
//     authFailed: boolean;
//     authFailedMessage: string | undefined;
// }

// const layout = {
//     labelCol: { span: 8 },
//     wrapperCol: { span: 8 },
// };

// const tailLayout = {
//     wrapperCol: { offset: 8, span: 16 },
// };

// const { Option } = Select;



// function onFinish() {

// }

export function Login() {
    // const authFailed = false;
    // const authFailedMessage = '';
    const authenticate = useStoreActions(state => state.auth.authenticate);
    const setBusy = useStoreActions(state => state.auth.setBusy);

    const error = useStoreState(state => state.auth.error);
    const isBusy = useStoreState(state => state.auth.busy);
    const user = useStoreState(state => state.auth.user);
    let rememberChoice: boolean = false;

    if (user) {
        return <Redirect to="/" />
    }

    const onRememberChange = (event) => {
        rememberChoice = event.target.checked;
    }

    const handleSubmit = async (payload) => {
        setBusy(true);
        payload.remember = rememberChoice;
        authenticate(payload);
    };

    const style = {
        container: {
            position: 'absolute' as 'absolute',
            background: '#ecf0f1',
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center'
        }
    };

    return (
        // <Layout style={style.container} className="login-form-wrapper">
        <Layout className="login-form-wrapper">
            <div className="logo-wrapper">
                <img className="loginLogo" src={logo} alt="login_logo" />
                <p className="page-title">Comunity Sheba</p>
            </div>
            <div className="login-form">
                <p className="page-sub-title">Sign in </p>
                <Form name="login" onFinish={handleSubmit}>
                    <Form.Item style={{ width: '330px' }} name='username' rules={[{ required: true, message: 'Please input your username or email!' }]}>
                        <Input
                            name="username"
                            style={{ width: '330px' }}
                            placeholder="Your username"
                            prefix={<UserOutlined className="site-form-item-icon" />}
                            suffix={
                                <Tooltip title="username">
                                    <InfoCircleOutlined style={{ color: 'rgba(0,0,0,.45)' }} />
                                </Tooltip>
                            }
                        />
                    </Form.Item>

                    <Form.Item style={{ width: '330px' }} name='password' rules={[{ required: true, message: 'Please input your password!' }]}>
                        <Input.Password
                            name="password"
                            style={{ width: '330px' }}
                            placeholder="Password"
                            prefix={<UserOutlined className="site-form-item-icon" />}
                            suffix={
                                <Tooltip title="password">
                                    <InfoCircleOutlined style={{ color: 'rgba(0,0,0,.45)' }} />
                                </Tooltip>
                            }
                        />
                    </Form.Item>

                    <Row justify="space-between">
                        <Col>
                            <Checkbox onChange={onRememberChange}>Remember me</Checkbox>
                        </Col>
                        <Col>
                            <Button type="primary" htmlType="submit" disabled={isBusy}>
                                {isBusy ? <Spin indicator={antIcon} /> : 'Login'}
                            </Button>

                        </Col>
                    </Row>

                    {
                        error ? <Alert style={{ marginTop: '20px' }} message={error} type="error" /> : null
                    }
                    <hr />
                    <div className="loginIcons">
                        <HomeOutlined />  <span style={{ marginLeft: "5px" }}>NY City, USA</span> <br />
                        <MailOutlined /><span style={{ marginLeft: "5px" }}> <a style={{ color: "black" }} href="mailto:example@example.com">example@example.com</a> </span>
                        <span style={{ float: "right" }}>  <PhoneOutlined style={{ marginLeft: "5px" }} /> <a style={{ color: "black" }} href="tel:+8801711111111">+8801711-111111</a></span>
                    </div>
                    <div className="signUp">
                       Not a member, <Link to="/signup">SIGN UP</Link>
                    </div>
                </Form>
                {/* <div className="footer">
                    <span style={{ marginRight: "8px" }}> <a href='http://aws.okkhor.org'>My Dashboard</a> : Just a dashboard </span>
                </div> */}
            </div>
        </Layout>
    );
}
