import React from 'react';
import { useStoreActions, Actions } from "easy-peasy";
import { StoreModel } from "./store/store";
import { Button, Card, Col, Popover, Row, Typography } from 'antd';
import Avatar from 'antd/lib/avatar/avatar';
import { UserOutlined } from '@ant-design/icons';
import { useStoreState } from './store/hooks/easyPeasy';

const { Text } = Typography;

export default function TopNavBar() {

  const logout = useStoreActions(
    (actions: Actions<StoreModel>) => actions.auth.logout
  );
  const logoutclear = useStoreActions(
    (actions: Actions<StoreModel>) => actions.auth.logoutclear
  );

  const user = useStoreState(state => state.auth.user)

  const logoutfunc = () => {
    logoutclear(user?.access_token);
    logout("");
  }
  const profilePopover = (onLogout, userData) => (
    <Card
      style={{ width: 300, marginTop: 16 }}
      actions={[
        <Button danger onClick={onLogout}>
          Logout
            </Button>,
      ]}
    >
      <div style={{ textAlign: "center" }}>
        <Avatar size={100} icon={<UserOutlined />} />
      </div>
      <div style={{ textAlign: "center", marginTop: 12 }}>
        <Text strong>Username: {userData?.username} </Text>
      </div>
    </Card>
  );
  return (
    <>
      <Row>
        <Col xs={24} sm={24} md={24} lg={23} xl={23}>
          <h3 id="pageTitle">Comunity Sheba</h3>
        </Col>
        <Col xs={24} sm={24} md={24} lg={1} xl={1} >
          <Popover
            content={profilePopover(logoutfunc, user)}
            placement="bottomLeft"
            trigger="click"
          >
            <Avatar
              icon={<UserOutlined />}
              size="large"
              style={{ marginRight: 5 }}
              className="pointer"
            />
          </Popover>
        </Col>
      </Row>

    </>
  );
}